.errorWrap {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap:20px;
}
.errorText {
  margin: 0;
  font-size: 50px;
}
.returnBtn {
  font-size: 30px;
  text-decoration: none;
  color: #000;
  transition: font-size 0.3s ease
}
.returnBtn:hover {
  color: #ff0090;
  font-size: 35px;
}
